/* Text Colors */
.text-primary {
  color: #28a745;
}

.text-secondary {
  color: #0284d0;
}

/* Button */
.btn {
  cursor: pointer;
  display: inline-block;
  padding: 5px 15px;
  color: #fff;
  background-color: #28a745;
  border-radius: 5px;
  border: none;
  outline: none;
  font-size: 14px;
  text-align: center;
  cursor: pointer;
}

.btn:hover {
  opacity: 0.7;
}

